import React, { useState, useEffect } from "react";
import { useTheme } from "@material-ui/core/styles";
//import library
import {
  Button,
  TextField,
  Typography,
  Grid,
  Box,
  InputAdornment,
  FormControl,
  InputLabel,
  Select,
  IconButton,
  Tooltip,
  Dialog,
} from "@material-ui/core";
import AddIcon from "@material-ui/icons/Add";
//import project
import VNDInput from "../../../../../../components/TextField/NumberFormatCustom";
// import img
import avaUpload from "../../../../../../assets/img/product/default-product.png";
import barcodeIcon from "../../../../../../assets/img/icon/barcode1.png";
import AddCategory from "../../../Category/Category";
import useStyles from "./styles";
import productApi from "../../../../../../api/productApi";
import { useDispatch, useSelector } from "react-redux";
import { statusAction } from "../../../../../../store/slice/statusSlice";
import { useFormik } from "formik";
import * as Yup from "yup";
const UploadImages = (img) => {
  return (
    <Box
      component="img"
      sx={{
        height: 70,
        width: 70,
        marginLeft: 7,
        marginRight: 7,
        borderRadius: 2,
      }}
      src={avaUpload}
    />
  );
};
const UpdateInventory = (props) => {
  const dispatch = useDispatch();
  const { handleClose, open } = props;
  const [openAddCategory, setOpenAddCategory] = useState(false);
  const handleCloseCategory = () => setOpenAddCategory(false);
  const [categoryList, setCategoryList] = useState([
    {
      uuid: "",
      parent_category_id: null,
      name: "",
      created_at: "",
      updated_at: "",
    },
  ]);
  const [images, setImages] = useState([]);
  const [display, setDisplay] = useState([]);
  useEffect(()=>{
    const displayList = props.productInfo.images.map((img) => ({link:img.url,isUrl: true}))
    setDisplay(displayList)
  },[props.productInfo.images])
  const addImageHandler = (e) => {
    console.log(e.target.files[0]);
    console.log(URL.createObjectURL(e.target.files[0]));
    setImages([...images, e.target.files[0]]);
    setDisplay([
      ...display,
      {
        index: images.length,
        link: URL.createObjectURL(e.target.files[0]),
        isUrl: false,
      },
    ]);
  };
  const productFormik = useFormik({
    enableReinitialize: true,
    initialValues: {
      name: props.productInfo.name,
      barcode: props.productInfo.bar_code ? props.productInfo.bar_code : '',
      importedPrice: props.productInfo.standard_price || 0,
      salesPrice: props.productInfo.list_price || 0,
      category: props.productInfo.category.uuid || 0,
      unit: props.productInfo.quantity_per_unit || '',
      re_order_point: props.productInfo.min_reorder_quantity || 0,
      product_code: props.productInfo.product_code || '',
    },
    validationSchema: Yup.object({
      name: Yup.string().required("Nháº­p tÃªn sáº£n pháº©m "),
      importedPrice: Yup.number().required("Nháº­p giÃ¡ vá»n").moreThan(0, "GiÃ¡ vá»n pháº£i lá»n hÆ¡n khÃ´ng"),
      salesPrice: Yup.number().required("Nháº­p giÃ¡ bÃ¡n").moreThan(0, "GiÃ¡ bÃ¡n pháº£i lá»n hÆ¡n khÃ´ng"),
      re_order_point: Yup.number("Pháº£i lÃ  má»t sá»").moreThan(-1, "Sá» lÆ°á»£ng Äáº·t hÃ ng láº¡i khÃ´ng ÄÆ°á»£c Ã¢m"),
    }),
  })
  const info = useSelector((state) => state.info);
  const store_uuid = info.store.uuid;

  const theme = useTheme();
  const classes = useStyles(theme);
  const updateProductHandler = async () => {
    handleCloseAndReset();
    try {
      var bodyFormData = new FormData();
      bodyFormData.append("name", productFormik.values.name.toString());
      bodyFormData.append("list_price", productFormik.values.importedPrice.toString());
      bodyFormData.append("standard_price",productFormik.values.salesPrice.toString());
      bodyFormData.append("bar_code", productFormik.values.barcode.toString());
      bodyFormData.append("product_code", productFormik.values.product_code.toString());
      bodyFormData.append("quantity_per_unit", productFormik.values.unit.toString());
      bodyFormData.append(
        "min_reorder_quantity",
        productFormik.values.re_order_point.toString()
      );

      bodyFormData.append(
        "category_uuid",
        productFormik.values.category.toString()
      );
      const img_url = display.reduce((prev,cur) =>{
        if(cur.isUrl){
          return prev.concat(cur.link,",")
        }
        return prev
      },"")
      bodyFormData.append("img_urls", img_url);

      images.forEach((image) => bodyFormData.append("images[]", image));
      const response = await productApi.updateProduct(
        store_uuid,
        props.productInfo.uuid,
        bodyFormData
      );
      dispatch(statusAction.successfulStatus("Sá»­a sáº£n pháº©m thÃ nh cÃ´ng"));
      props.setReload();
    } catch (error) {
      console.log(error)
      dispatch(statusAction.failedStatus("Sá»­a tháº¥t báº¡i"));
    }
  };
  useEffect(() => {
    const fetchCategoryList = async () => {
      try {
        const response = await productApi.getAllCategory(store_uuid);
        setCategoryList(response.data);
      } catch (error) {
        console.log(error);
        return [];
      }
    };
    fetchCategoryList();
  }, [store_uuid, props.productInfo]);
  
  const handleCloseAndReset = () => {
    handleClose();
    productFormik.resetForm()
    clearAllImages();
  };
  const clearAllImages = () => {
    setDisplay([]);
    setImages([]);
  };
  const clearImage = (displayImage) => {
    setDisplay(display.filter((img) => img != displayImage));
    if (!displayImage.isUrl) {
      setImages(images.filter((image, index) => index !== displayImage.index));
    }
  };
  return (
    <Dialog
      open={open}
      onClose={handleClose}
      aria-labelledby="form-dialog-title"
    >
      <AddCategory open={openAddCategory} handleClose={handleCloseCategory} />
      <Box className={classes.root}>
        <Typography
          className={classes.headerTitle}
          variant="h5"
          gutterBottom
          style={{ marginBottom: 20 }}
        >
          Chá»nh sá»­a sáº£n pháº©m
        </Typography>
        <Grid
          container
          direction="row"
          justifyContent="space-around"
          spacing={2}
        >
          <Grid item sm={7} xs={12}>
            <TextField
              required
              label="TÃªn sáº£n pháº©m"
              variant="outlined"
              fullWidth
              size="small"
              name="name"
              onChange={productFormik.handleChange}
              value={productFormik.values.name}
              error={productFormik.touched.name && productFormik.errors.name}
              helperText={
                productFormik.touched.name ? productFormik.errors.name : null
              }
              onBlur={productFormik.handleBlur}
              type="text"
            />
            <TextField
              label="MÃ£ sáº£n pháº©m (tá»± Äá»ng)"
              variant="outlined"
              fullWidth
              size="small"
              className={classes.margin}
              name="product_code"
              onChange={productFormik.handleChange}
              value={productFormik.values.product_code}
            />
            <TextField
              label="MÃ£ váº¡ch"
              variant="outlined"
              fullWidth
              size="small"
              name="barcode"
              onKeyDown={(e) => {}}
              onChange={productFormik.handleChange}
              value={productFormik.values.barcode}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <Box
                      component="img"
                      sx={{ height: 25, width: 25 }}
                      src={barcodeIcon}
                    />
                  </InputAdornment>
                ),
              }}
              className={classes.margin}
            />
            <TextField
              label="ÄÆ¡n vá»"
              variant="outlined"
              fullWidth
              size="small"
              className={classes.margin}
              name="unit"
              onChange={productFormik.handleChange}
              value={productFormik.values.unit}
            />

            <Box className={`${classes.box} ${classes.margin}`}>
              <FormControl required size="small" variant="outlined" fullWidth>
                <InputLabel htmlFor="category">Danh má»¥c</InputLabel>
                <Select
                  native
                  label="Danh má»¥c"
                  id="category"
                  name="category"
                  value={productFormik.values.category}
                  onChange={productFormik.handleChange}
                  onBlur={productFormik.handleBlur}
                >
                  {categoryList.map((category) => (
                    <option key={category.uuid} value={category.uuid}>
                      {category.name}
                    </option>
                  ))}
                </Select>
              </FormControl>
              <Tooltip title="ThÃªm danh má»¥c">
                <IconButton
                  size="small"
                  style={{ marginLeft: 5 }}
                  onClick={() => setOpenAddCategory(true)}
                >
                  <AddIcon color="primary" />
                </IconButton>
              </Tooltip>
            </Box>
          </Grid>
          <Grid item sm={5} xs={12}>
            <VNDInput
              required
              label="GiÃ¡ bÃ¡n"
              variant="outlined"
              fullWidth
              size="small"
              name="salesPrice"
              value={productFormik.values.salesPrice}
              onChange={productFormik.handleChange}
              error={
                productFormik.touched.salesPrice &&
                productFormik.errors.salesPrice
              }
              helperText={
                productFormik.touched.salesPrice
                  ? productFormik.errors.salesPrice
                  : null
              }
              onBlur={productFormik.handleBlur}
            />
            <VNDInput
              required
              label="GiÃ¡ vá»n"
              variant="outlined"
              fullWidth
              size="small"
              name="importedPrice"
              value={productFormik.values.importedPrice}
              onChange={productFormik.handleChange}
              error={
                productFormik.touched.importedPrice &&
                productFormik.errors.importedPrice
              }
              helperText={
                productFormik.touched.importedPrice
                  ? productFormik.errors.importedPrice
                  : null
              }
              onBlur={productFormik.handleBlur}
              className={classes.margin}
            />
            <TextField
              label="Sá» lÆ°á»£ng Äáº·t hÃ ng láº¡i"
              variant="outlined"
              fullWidth
              size="small"
              className={classes.margin}
              name="re_order_point"
              value={productFormik.values.re_order_point}
              onChange={productFormik.handleChange}
              error={
                productFormik.touched.re_order_point &&
                productFormik.errors.re_order_point
              }
              helperText={
                productFormik.touched.re_order_point
                  ? productFormik.errors.re_order_point
                  : null
              }
              onBlur={productFormik.handleBlur}
            />
          </Grid>
        </Grid>
        <Grid container spacing={2}>          <Grid item xs>
            <Box
              display="flex"
              flexDirection="row"
              alignItems="center"
              style={{ marginTop: 10 }}
            >
              {display.map((img) => (
                <Tooltip title="XÃ³a táº¥t cáº£ hÃ¬nh áº£nh">
                  <Button size="small" onClick={() => clearImage(img)}>
                    <Box
                      component="img"
                      sx={{
                        height: 70,
                        width: 70,
                        marginLeft: 7,
                        marginRight: 7,
                        borderRadius: 2,
                      }}
                      src={img.link}
                    />
                  </Button>
                </Tooltip>
              ))}
              {display.length === 0 ? <UploadImages /> : null}
              <IconButton
                disabled={display.length > 3 ? true : false}
                color="primary"
                size="medium"
                component="label"
              >
                <input type="file" hidden onChange={addImageHandler} />
                <AddIcon />
              </IconButton>
            </Box>
          </Grid>
          <Grid
            item
            xs={12}
            style={{
              display: "flex",
              flexDirection: "row",
              justifyContent: "flex-end",
            }}
          >
            <Button
              onClick={handleCloseAndReset}
              variant="contained"
              size="small"
              color="secondary"
              style={{ marginRight: 20 }}
            >
              Huá»·
            </Button>
            <Button
              onClick={() => {
                updateProductHandler();
              }}
              variant="contained"
              size="small"
              color="primary"
              disabled = {!(productFormik.isValid ) }
            >
              Sá»­a
            </Button>
          </Grid>
        </Grid>
      </Box>
    </Dialog>
  );
};

export default UpdateInventory;
