import React from "react";
import { useTheme, makeStyles, styled ,lighten,darken} from "@material-ui/core/styles";
import useMediaQuery from "@material-ui/core/useMediaQuery";
//import library
import {
  AppBar,
  Toolbar,
  IconButton,
  Typography,
  Box,
  Button,
  Grid,
  Tooltip,
  ButtonBase,
  Avatar,
  Badge,
  List,
  Drawer,
  ListItem,
  ListItemText,
  Divider
} from "@material-ui/core";
//import icons
import MenuIcon from "@material-ui/icons/Menu";
import HoverMenuBtn from "../../../components/Button/HoverMenuBtn";
import { Link, useRouteMatch } from "react-router-dom";
import AccountCircleTwoToneIcon from '@material-ui/icons/AccountCircleTwoTone';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import AccountCircleRoundedIcon from '@material-ui/icons/AccountCircleRounded';
import ShoppingCartTwoToneIcon from '@material-ui/icons/ShoppingCartTwoTone';
import ShoppingCartIcon from '@material-ui/icons/ShoppingCart';
import {ColorButton,ColorOutlineButton} from "../../../components/Button/ColorButton"
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
    appBar: {
        paddingLeft: 20,
        paddingRight: 20,
        boxShadow: "none",
    },
    toolBar: {
        color: theme.customization.themeGreyText,
    },
    btnNav: {
        textTransform: "none",
        marginRight: 10,
    },   
}));

const NavBar = (props) => {
    const {storeInfo,logo, handleClickItem,category} = props;
    const {buttonLogin,buttonCart,navColor,textNav} = props.webInfo.navBar;
    const mainColor=`rgba(${ props.webInfo.mainColor.r }, ${ props.webInfo.mainColor.g }, ${ props.webInfo.mainColor.b }, ${ props.webInfo.mainColor.a })`

    // 
    let { url } = useRouteMatch();
    const theme = useTheme();
    const classes = useStyles(theme);
    const matchDownSm = useMediaQuery(theme.breakpoints.down("sm"));

    // customization
    function handleColor (type) {
      if(type===0){return '#000'}
      else if (type===1){return '#fff'}
      else if (type===3){return mainColor}
      else{return theme.darkTextPrimary }
    }
    const textColor = handleColor(parseInt(textNav[0]));
    const textSize = parseInt(textNav[1])
    const textBold = parseInt(textNav[2])
  
    //
    const [state, setState] = React.useState(false);
    const toggleDrawer = (open) => {
      setState(open);
    };

    const drawer = () => (
      <div
        className={clsx(classes.list)}
        role="presentation"
        onClick={()=>toggleDrawer(false)}
        onKeyDown={()=>toggleDrawer(false)}
      >
        <List>
          {[{text:'Trang chá»§', link:''}, {text:'Sáº£n pháº©m', link:''}, {text:'Cá»­a hÃ ng', link:'storeInfo'}].map((tab, index) => {
            if(tab.text !== "Sáº£n pháº©m" ){
              return(
              <>
                <ListItem button key={tab.text} style={{width:220, margin:7}}
                component={Link}
                to={`/store/${props.storeInfo?.web_page}/${tab.link}`}>
                <Typography style={{ color: '#000',  fontWeight: textBold,  fontSize: textSize,}}>
                  {tab.text} 
                </Typography>
              </ListItem>
                <Divider />
              </>
              )
            }else{
              return( /* Sá»­a Link Sáº£n Pháº©m Sau */
              <>
              <ListItem button key={tab.text}  style={{width:220, margin:7}}>  
                <Typography   style={{ color: '#000',  fontWeight: textBold, fontSize: textSize,}}>
                  {tab.text} 
                </Typography>
               </ListItem>
               <Divider />
            </>)
            }})}
        </List>
      </div>
    );

    return (
      <AppBar
        position="fixed"
        className={classes.appBar}
        style={{
          background: parseInt(navColor) ? mainColor : theme.palette.background.paper,
        }}
      >
        <Toolbar
          className={classes.toolBar}
          style={{
            background: parseInt(navColor) ? mainColor : theme.palette.background.paper,
          }}
        >
          <Grid
            container
            direction="row"
            alignItems="center"
            justifyContent="space-between"
          >
            <Grid item sm={2} 
            component={Link}
            to={`${url}`}
            >
              <img src={logo} style={{height:45,marginTop:-5,marginBottom:-5}}/>
            </Grid>
            {/* Small screen */}
            {matchDownSm ? (
              <Grid>
                <IconButton
                  aria-label="open drawer"
                  onClick={()=>toggleDrawer( true)}
                  edge="start"
                  style={{ marginBottom: -5 }}
                >
                  <MenuIcon />
                </IconButton>
                <React.Fragment key={'right'}>
                  <Drawer anchor="right" open={state} onClose={()=>toggleDrawer(false)}>
                    {drawer()}
                  </Drawer>
                </React.Fragment>
              </Grid>
            ) : (
              //Nav bar
              <Grid container item sm={10} direction="row" alignItems="center">
                <Grid container item sm={8} direction="row">
                  <Button
                    className={classes.btnNav}
                    component={Link}
                    to={`${url}`}
                    style={{
                      color: textColor,
                      fontWeight: textBold,
                      fontSize: textSize,
                    }}
                  >
                    Trang chá»§
                  </Button>
                  <HoverMenuBtn
                    className={classes.btnNav}
                    handleClickItem={handleClickItem}
                    category={category}
                    // style
                    textColor={textColor}
                    textSize={textSize}
                    textBold={textBold}
                  >
                  </HoverMenuBtn>
                  <Button
                    className={classes.btnNav}
                    component={Link}
                    to={`${url}/promotion`}
                    style={{
                      color: textColor,
                      fontWeight: textBold,
                      fontSize: textSize,
                    }}
                  >
                    Khuyáº¿n mÃ£i
                  </Button>
                  <Button
                    className={classes.btnNav}
                    component={Link}
                    to={`${url}/storeInfo`}
                    style={{
                      color: textColor,
                      fontWeight: textBold,
                      fontSize: textSize,
                    }}
                  >
                    Cá»­a hÃ ng
                  </Button>
                  {/* <Button
                    className={classes.btnNav}
                    component={Link}
                    to={`/store/${props.storeInfo?.web_page}/aboutUs`}
                    style={{
                      color: textColor,
                      fontWeight: textBold,
                      fontSize: textSize,
                    }}
                  >
                    Giá»i thiá»u
                  </Button> */}
                </Grid>

                {/* NÃºt Äáº·ng nháº­p */}
                <Grid
                  container
                  item
                  sm={4}
                  direction="row"
                  justifyContent="flex-end"
                >
                  {parseInt(buttonLogin) == 0? 
                  <>
                   <ColorOutlineButton
                   mainColor={mainColor}
                   navColor={parseInt(navColor)}
                   className={classes.btnNav}
                   variant="outlined"
                   style={{ borderRadius: 20, marginBottom: 5 }}
                 >
                   ÄÄng nháº­p
                 </ColorOutlineButton>
                 <ColorButton
                   mainColor={mainColor}
                   navColor={parseInt(navColor)}
                   className={classes.btnNav}
                   variant="contained"
                   color="secondary"
                   style={{
                     borderRadius: 20,
                     marginLeft: 10,
                     marginBottom: 5,
                   }}
                 >
                   ÄÄng kÃ½
                 </ColorButton>
                 </>
                  : 
                  <AccountCircleRoundedIcon  style={{width:40, height:40, color:parseInt(navColor) === 0? mainColor:"#fff"}}/>
                  }
                  {/* NÃºt cart */}
                  {parseInt(buttonCart) === 1 ? 
                    <div style={{backgroundColor:parseInt(navColor) === 0? mainColor:'#fff',width:37, height:37,marginTop:2,paddingTop:7, paddingLeft:7,borderRadius:40,marginLeft:15}}
                    component={Link}
                    to={`${url}/cart`}
                    >
                      <Tooltip title="Giá» hÃ ng">
                        <StyledBadge color="error" badgeContent={3}>
                          <ShoppingCartIcon  style={{color:parseInt(navColor) === 0?"#fff":mainColor}}/>
                        </StyledBadge>
                      </Tooltip>
                  </div>
                 :null
                  }
                 
                </Grid>
              </Grid>
            )}
          </Grid>
        </Toolbar>
      </AppBar>
    );
}

export default NavBar

const StyledBadge = styled(Badge)(({ theme }) => ({
  "& .MuiBadge-badge": {
    border: `1px solid #000`,
    padding: "0 4px",
  },
}));